import { Component, OnInit } from '@angular/core';
import { ModalController, AnimationController, AlertController } from '@ionic/angular';
import { RecordatorioModalPage } from '../../Modales/recordatorio-modal/recordatorio-modal.page';
import { ApiSeguimientoService } from '../../../providers/Api/Seguimiento.service';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';
import { RecordatoriosService } from '../../../providers/Api/Recordatorios.service';
import { ToastServices } from 'src/app/providers/Utilidades/ToastService.service';
import { trigger, transition, style, animate } from '@angular/animations';
import { EntradasServices } from '../../../providers/cargas/Entradas.service';

@Component({
  selector: 'app-recordatorio',
  templateUrl: './recordatorio.page.html',
  styleUrls: ['./recordatorio.page.scss'],
  animations: [
    trigger('entrada', [
      transition(':enter', [
        style({
          opacity: 0
        }),
        animate('300ms', style({opacity: 1}))
      ])
    ])
  ]
})
export class RecordatorioPage implements OnInit {
  
  lista = [];
  vista = '1';
  recordatorio;
  role = '';
  texto = '';

  constructor(
    private modalCtrl: ModalController,
    private animationCtrl: AnimationController,
    private apiSeguimiento: ApiSeguimientoService,
    private apiRecordatorios: RecordatoriosService,
    private almacenamiento: InicioService,
    private miToast: ToastServices,
    private alertCtrl: AlertController,
    private entrada: EntradasServices
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.listaRecordatorios();
  }

  async refrescando( event ) {
    const res = await this.listaRecordatorios();
    if (res) {
      console.log(res);
      event.target.complete();
    } else {
      event.target.complete();
    }
  }

  async listaRecordatorios() {
    const info = await this.almacenamiento.obtenerToken();

    if (info) {
        this.role = info.role;
        if (info.role !== 'USER_ROLE') {
          this.apiRecordatorios.getRecordatorio(info.token).subscribe((data: any) => {
            console.log(data);
            if (data.exe) {
              data.response.forEach((item) => {
                item.checked = false;
              })
              this.lista = data.response;
            } else {
              this.miToast.alertApplication('No se pudo traer los recordatorios, intentelo más tarde', false);
            }
          })
        } else {
          this.apiRecordatorios.getRecordatorioPersona(info.token).subscribe((data: any) => {
            console.log(data);
            if (data.exe) {
              data.response.forEach((item) => {
                item.checked = false;
              })
              this.lista = data.response;
            } else {
              this.miToast.alertApplication('No se pudo traer los recordatorios, intentelo más tarde', false);
            }
          })
        }

        return true;
    }
  }

  opciones( item: any ){
    if (!this.recordatorio) {
      this.vista = '2';
      item.checked = !item.checked;
      this.recordatorio = item;
    }
  }

  async eliminarRecordatorio() {
      const alert = await this.alertCtrl.create({
        header: 'Borrar Recordatorio',
        message: '¿Está seguro de eliminar este recordatorio de forma permanente?',
        buttons: [
          {
            text: 'Cancelar',
            role: 'cancel',
          },
          {
            text: 'Aceptar',
            handler: async () => {
              const info = await this.almacenamiento.obtenerToken();

              if (info) {
                this.apiRecordatorios.deleteRecordatorio(info.token, this.recordatorio._id).subscribe((data: any) => {
                  if (data.exe) {
                    this.miToast.alertApplication('El recordatorio ha sido eliminado', true);
                    this.listaRecordatorios();
                  } else {
                    this.miToast.alertApplication('No se pudo borrar el recordatorio, intentelo más tarde', false);
                  }
                  this.recordatorio = false;
                })
              }
            }
          }
        ]
      });

      return await alert.present();
  }

  cerrarOpt() {
    this.vista = '1';
    this.lista.forEach((item) => item.checked = false);
    this.recordatorio = false;
  }

  async modal(type: string) {
    const enterAnimation = (baseEl: any) => {
      const backdropAnimation = this.animationCtrl.create()
        .addElement(baseEl.querySelector('ion-backdrop'))
        .fromTo('opacity', '0.01', 'var(--backdrop-opacity)');

      const wrapperAnimation = this.animationCtrl.create()
        .addElement(baseEl.querySelector('.modal-wrapper'))
        .keyframes([
          { offset: 0, opacity: '0', transform: 'scale(0)  translateX(300px)' },
          { offset: 1, opacity: '0.99', transform: 'scale(1) translateX(0)' }
        ]);

      return this.animationCtrl.create()
        .addElement(baseEl)
        .easing('ease-out')
        .duration(300)
        .addAnimation([backdropAnimation, wrapperAnimation]);
    }

    const leaveAnimation = (baseEl: any) => {
      return enterAnimation(baseEl).direction('reverse');
    }
    const mimodal = await this.modalCtrl.create({
      component: RecordatorioModalPage,
      componentProps: {
        type,
       // recorType: this.recordatorio.tipo,
        recordatorioData: this.recordatorio
      },
      mode: 'ios',
      enterAnimation,
      leaveAnimation
    })

    await mimodal.present();

    const cerrado = await mimodal.onWillDismiss();

    if (cerrado) {
        this.recordatorio = false;
        this.vista = '1';
        this.listaRecordatorios();
    }
  }

  informacion() {
      this.entrada.crearEntrada();
  }

  filtrar( event ) {
    this.texto = event.detail.value;
  }

  ver(recor){
    this.entrada.mostrarSoloUna(recor)
  }
}
